.m-reviews {
	padding-top: 220px;
	padding-bottom: 220px;
	color: var(--clr-secondary);
	background-color: var(--clr-backg-secondary);
}

.myslider {
	width: 100%;
	min-height: 345px;
}

.s-btn-prev,
.s-btn-next {
	--swiper-navigation-color: #fff;
	color: var(--swiper-navigation-color);

	&:is(:hover, :focus) {
		--swiper-navigation-color: var(--clr-neutral);
		color: var(--swiper-navigation-color);
		transition: color 0.3s ease;
	}
}

.m-reviews-slide {
	display: flex;
	align-items: streach;
}

.m-reviews__content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	max-width: 785px;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
}

.review__headline {
	font-weight: 600;
	font-size: 36px;
	text-align: center;
	color: var(--clr-neutral);
	margin-bottom: 20px;

	&:before {
		display: block;
		content: url(../img/quotes.svg);
		width: 83px;
		height: 58px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 34px;
	}
}
.review__text {
	line-height: 2.5;
	text-align: center;
	margin-bottom: 40px;
}
.review__reviewer {
	font-weight: 600;
	font-size: 24px;
	letter-spacing: 0.25em;
	text-align: center;
	color: var(--clr-neutral);
	text-transform: uppercase;
}

@media (max-width: 975.98px) {
	.m-reviews {
		padding-top: 75px;
		padding-bottom: 75px;
	}
}

@media (max-width: 519.98px) {
	.m-reviews {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	.review__headline {
		font-size: 30px;
		margin-bottom: 15px;
	}
	.swiper-button-next,
	.swiper-button-prev {
		top: auto;
		bottom: 0;
	}
}
