.dialog {
	width: calc(100% - 40px);
	max-width: 450px;
	border: none;
	inset: 0;
	position: fixed;
}

.dialog::backdrop {
	background-color: rgb(0 0 0 / 0.6);
	-webkit-backdrop-filter: blur(10px);
	backdrop-filter: blur(10px);
}
.form-dialog-btn {
	display: flex;
	align-items: center;
}

.scroll-lock {
	overflow: hidden;
}

.btn-close {
	display: flex;
	align-items: center;
	background: none;
	margin-left: auto;
}

.icon-close {
	width: 35px;
	height: 35px;
}

.dialog .input__list {
	grid-template-columns: 1fr;
}
.dialog .form-input {
	padding: 10px 10px;
}
.dialog .form-btn {
	width: 250px;
	height: 75px;
}
@media (max-width: 579px) {
	.dialog {
		/* Сдвигаем диалоговые окна ближе к низу страницы, чтобы они не перекрывали кнопки на демке */
		margin-bottom: 30px;
		bottom: 0;
	}
}
