.m-lunchmenu {
	padding-top: 110px;
	padding-bottom: 80px;
	&__title {
		font-weight: 700;
		font-size: clamp(30px, 3.5vw, 40px);
		line-height: 2.2;
		text-transform: uppercase;
		margin-bottom: 20px;
		&::after {
			display: block;
			content: "";
			width: 108px;
			height: 2px;
			background-color: var(--clr-neutral);
			transform: translateX(-54px);
		}
	}
	&__text {
		color: #7f7f7f;
	}
}
.m-lunchmenu__inner {
	display: flex;
	flex-direction: column;
	gap: 30px;
}
.m-lunchmenu__row {
	display: flex;
	align-items: center;
	gap: 30px;
}

.m-lunchmenu__row--revers {
	flex-direction: row-reverse;
}
.m-lunchmenu__col-text {
	max-width: 470px;
	width: 100%;
}
.m-lunchmenu__col-img {
	width: 100%;
}

@media (max-width: 519.98px) {
	.m-lunchmenu {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	.m-lunchmenu__row {
		flex-direction: column;
	}
}
