.list-social {
	display: flex;
	gap: 25px;
}

.icon-social {
	--clr-icon-primary: #ffffff;
	width: 48px;
	height: 48px;
	fill: var(--clr-icon-primary);
	stroke: var(--clr-icon-primary);
	transition: all 0.3s ease;

	&:hover {
		--clr-icon-primary: #d3b47f;
	}
}

.m-footer .icon-social {
	--clr-icon-primary: #000;
	&:hover {
		--clr-icon-primary: #d3b47f;
	}
}
