.m-header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 42;
	background-color: transparent;
	padding-top: 55px;
	padding-bottom: 55px;
	transition: all 0.3s ease;
	&__inner {
		display: flex;
		align-items: center;
	}
}
.m-header__scroll {
	padding-top: 25px;
	padding-bottom: 25px;
	background-color: var(--clr-backg-secondary);
	transition: all 0.3s ease;
}

.m-header__hidden {
	transform: translateY(-100%);
}

.mobile-btn {
	display: none;
}
@media (max-width: 975.98px) {
	.mobile-btn {
		display: block;
		width: 36px;
		height: 24px;
		background: none;
		margin-left: auto;
	}
	.icon-mobile {
		fill: #fff;
		&:active {
			fill: #24090c;
		}
	}
}

@media (max-width: 519.98px) {
	.link-logo {
		width: 150px;
	}
}
