.m-gallery {
	padding-top: 68px;
	padding-bottom: 68px;
	color: var(--clr-secondary);
	background: rgb(58, 69, 116);
	background: linear-gradient(
		180deg,
		rgba(58, 69, 116, 1) 0%,
		rgba(58, 69, 116, 1) 60%,
		rgba(255, 255, 255, 1) 60%,
		rgba(255, 255, 255, 1) 100%
	);
}
.s-btn__group {
	height: 70px;
	position: relative;
	display: inline-flex;
	width: 170px;
}
.m-gallery__inner {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.m-gallery__box {
	margin-bottom: 50px;
}
.m-gallery__title {
	font-weight: 700;
	font-size: clamp(30px, 3.5vw, 48px);
	line-height: 2.2;
}

.m-gallery__subtitle {
	max-width: 570px;
}

.mygallery {
	width: 100%;
	max-height: 646px;
}

@media (max-width: 975.98px) {
	.m-gallery {
		padding-top: 30px;
		padding-bottom: 30px;
	}
	.mygallery {
		width: 100%;
		max-height: 330px;
	}
}
