.o-container {
	max-width: var(--container-width);
	margin: 0 auto;
	padding: 0 var(--container-offset);
}

.o-page__wrapper {
	overflow: hidden;
	position: relative;
	background-color: var(--clr-backg-primary);
}

.o-visually-hidden {
	position: absolute;
	clip: rect(0 0 0 0);
	width: 1px;
	height: 1px;
	margin: -1px;
}
