.m-welcome {
	min-height: 900px;
	display: grid;
	grid-template-areas: "welcome";
	z-index: 7;
	color: var(--clr-secondary);
}
.m-welcome__picture {
	position: relative;
	grid-area: welcome;
	background-color: rgba(0, 0, 0, 0.2);
	z-index: 1;

	&::after {
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		content: "";
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.5);
		z-index: 2;
	}
}

.m-welcome__picture img {
	min-height: 100%;
	max-width: 100%;
	width: 100%;
}

.m-welcome__wrapper {
	z-index: 7;
	grid-area: welcome;
	align-self: center;
}
.working-time {
	font-weight: 500;
	font-size: 24px;
	letter-spacing: 0.06em;
	margin-bottom: 24px;
	text-transform: uppercase;
}
.slogan {
	font-weight: 700;
	font-size: clamp(30px, 4.5vw, 64px);
	margin-bottom: clamp(25px, 3.5vw, 40px);
}
.m-welcome__row {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

@media (max-width: 767.98px) {
	.m-welcome__row {
		align-items: start;
		justify-content: space-between;
		flex-direction: column;
		gap: 40px;
	}
}
@media (max-width: 519.98px) {
	.working-time {
		font-size: 18px;
	}
}

