.m-weeklymenu {
	padding-top: 80px;
	padding-bottom: 90px;
}

.m-weeklymenu__row {
	display: flex;
	gap: 25px;
}
.m-weeklymenu__column:first-child {
	max-width: 470px;
	width: 100%;
}
.m-weeklymenu__column:last-child {
	position: relative;
	z-index: 1;
	width: 100%;
	display: flex;
	gap: 22px;
	align-items: center;
	justify-content: space-between;
	flex-basis: 100%;

	&::before {
		content: "";
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: -1;
		max-width: 471px;
		width: 100%;
		min-height: 502px;
		border: 2px solid #d3b47f;
	}
}

.m-weeklymenu__column:last-child .m-weeklymenu__picture:first-child {
	margin-top: 152px;
}
.m-weeklymenu__column:last-child .m-weeklymenu__picture:last-child {
	margin-bottom: 152px;
}
.m-weeklymenu__list {
	display: grid;
	gap: 20px;
}
.m-weeklymenu__item {
	padding-bottom: 20px;
	border-bottom: 1px solid #e7e7e7;
}

.m-weeklymenu__item-title {
	display: inline-block;
	font-weight: 600;
	font-size: 24px;
	margin-bottom: 15px;
}
.m-weeklymenu__item-row {
	display: flex;
	align-items: center;
	gap: 12px;
}
.m-weeklymenu__item-text {
	font-size: 18px;
}

.m-weeklymenu__item-dots {
	width: 40px;
	display: flex;
	align-items: center;
	justify-content: space-between;

	&::before,
	&::after {
		display: inline-block;
		content: "";
		width: 5px;
		height: 5px;
		border-radius: 50%;
		background-color: var(--clr-neutral);
	}
}

.dots {
	display: inline-block;
	width: 5px;
	height: 5px;
	border-radius: 50%;
	background-color: var(--clr-neutral);
}

@media (max-width: 975.98px) {
	.m-weeklymenu__row {
		flex-direction: column;
	}
}
@media (max-width: 519.98px) {
	.m-weeklymenu {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	.m-weeklymenu__column:last-child::before {
		min-height: 300px;
	}
	.m-weeklymenu__item-title {
		font-size: 18px;
	}
	.m-weeklymenu__item-row {
		flex-direction: column;
	}
}
