.rightside-menu {
	display: flex;
	align-items: center;
	flex-basis: 100%;
}
.menu {
	margin-left: auto;
}
.menu__list {
	display: flex;
	gap: 35px;
}
.menu__item {
	&-link {
		color: var(--clr-secondary);
		padding: 5px;
		font-weight: 500;
		&:hover {
			color: var(--clr-neutral);
			transition: color 0.3s ease;
		}
	}
}

.rightside-menu__close,
.rightside-menu .block-socials {
	display: none;
}
.link-phone {
	margin-left: auto;
	display: flex;
	align-items: center;
	color: #ffffff;

	&:is(:hover, :focus) {
		color: var(--clr-neutral);
	}
}
.icon-phone {
	width: 20px;
	height: 20px;
	stroke: var(--clr-neutral);
	fill: none;
	margin-right: 11px;
}

@media (max-width: 975.98px) {
	.rightside-menu {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		z-index: 42;
		max-width: 300px;
		width: 100%;
		background-color: var(--clr-backg-rightside);
		padding-top: 80px;
		padding-right: 45px;
		padding-bottom: 80px;
		padding-left: 45px;
		margin-right: 15px;
		box-shadow: -15px 0px 15px -3px rgba(0, 0, 0, 0.1);
		transform: translateX(1000px);
		transition: transform 0.3s ease;
		flex-direction: column;
		gap: 55px;
		min-height: 100vh;
	}

	.rightside-menu--open {
		transform: translateX(0);
		transition: transform 0.3s ease;
	}
	.rightside-menu__close {
		display: block;
		background: none;
		position: absolute;
		top: 46px;
		right: 4px;
	}
	.rightside-menu__close:hover .img-close {
		transform: rotate(45deg);
		transition: transform 0.3s ease-in-out;
	}
	.rightside-menu .block-socials {
		display: block;
	}

	.menu__list {
		flex-direction: column;
		gap: 25px;
	}
	.menu {
		margin-left: 0;
		width: 100%;
	}
	.link-phone {
		margin-left: 0;
	}
}
