textarea[name="message"] {
	resize: none;
}

.main-form {
	display: grid;
	gap: 20px;
}

.form-btn {
	width: 270px;
	height: 100px;
	font-weight: 500;
	font-size: 18px;
	line-height: 24px;
	color: inherit;
	background-color: var(--clr-backg-secondary);

	&:is(:hover) {
		color: var(--clr-primary);
		background-color: var(--clr-neutral);
		transition: all 0.3s ease;
	}
}

.main-form__wrapper,
.input__list {
	display: grid;
	gap: 20px;
}
.input__list {
	grid-template-columns: 1fr 1fr;
}
.input__item {
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.form-label {
	line-height: 2;

	span {
		color: red;
		margin-left: 5px;
	}
}

.form-input {
	line-height: 1.5;
	color: #7f7f7f;
	padding: 28px 16px;
}

.form-input._error {
	box-shadow: 0 0 15px red;
}

.form-textarea {
	max-height: 125px;
	overflow-y: scroll;
}
