.m-contacts {
	padding-top: 32px;
	padding-bottom: 50px;
}

.m-contacts__row {
	display: flex;
	gap: 25px;
}
.m-contacts__title {
	font-weight: 700;
	font-size: 48px;
	line-height: 88px;
	color: #000000;
	margin-bottom: 30px;
}
.m-contacts__column:first-child {
	max-width: 470px;
	width: 100%;
}
.m-contacts__column:last-child {
	flex-basis: 100%;
}
.m-contacts__icon {
	display: flex;
	align-items: center;
	justify-content: center;
	flex: 0 0 auto;
	width: 64px;
	height: 64px;
	border-radius: 50%;
	background-color: var(--clr-neutral);
}
.icon-contacts,
.icon-contacts.icon-phone {
	stroke: var(--clr-secondary);
	width: 20px;
	height: 20px;
}
.icon-contacts.icon-phone {
	position: relative;
	left: 5px;
}

.m-contacts__group {
	display: flex;
	flex-direction: column;
}

.m-contacts__name {
	color: #2e2e2e;
}
.m-contacts__text {
	font-weight: 500;
	font-size: 24px;
	line-height: 32px;
	color: #2e2e2e;
}

.m-contacts__item {
	display: flex;
	align-items: center;
	gap: 15px;
}

.m-contacts__list {
	display: flex;
	flex-direction: column;
	gap: 40px;
}

.m-contacts__form .form-btn {
	color: var(--clr-secondary);
}

@media (max-width: 975.98px) {
	.m-contacts__row {
		flex-direction: column;
	}
}

@media (max-width: 767.98px) {
	.m-contacts__form .input__list {
		grid-template-columns: 1fr;
	}
	.m-contacts__form .form-input {
		padding: 10px 10px;
	}
}

@media (max-width: 519.98px) {
	.m-contacts {
		padding-top: 20px;
		padding-bottom: 30px;
	}
	.m-contacts__title {
		font-size: 30px;
		line-height: 2;
		margin-bottom: 15px;
	}
	.m-contacts__text {
		font-size: 18px;
		line-height: 1.5;
	}
	.m-contacts__icon {
		width: 50px;
		height: 50px;
	}
}
